<template>
  <div id="mainComponent">
    <!-- <nav></nav> -->

    <div class="main">
        <div class="left-col">
            <p class="name">Andrew Inhwan Kim</p>
                
            <!-- <p class="jobtitle">Frontend Engineer</p> -->
            <vue-typer 
            class="jobtitle" 
            :text='["Frontend Engineer", "Web Developer", "UX Designer"]'
            :repeat="Infinity"
            :erase-delay='70'
            :pre-erase-delay='1000'
            erase-style='backspace'
            caret-animation='phase'
            ></vue-typer>

            <!-- <p class="location">Currently based in Northern Virginia</p> -->
            <!-- <p class="tagline">I have an eye for how it looks, but also the knowledge of why it behaves.</p> -->
            <div class="resume">
            <a id="aikresume" href="../static/aik_resume_18.pdf" target="_blank" rel="noopener noreferrer">View my Resume</a>
            <font-awesome-icon :icon="['far', 'file-pdf']" size="2x" class="far" color="white"/>
          </div>
        </div>  
        <div class="right-col profile">
        <!-- <img src="../assets/profile.jpg" alt="Profile Photo" class="profile-photo">      -->
        <!-- <h1>Welcome to my portfolio</h1> -->
          
        </div>

    </div>
        <div class="wave-bottom-container">
          <div class="wave">        
          </div>
          <div class="wave wave2">          
          </div>
        </div>

  </div>
</template>

<script>
import { VueTyper } from 'vue-typer'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'


export default {
  name: 'MainSection',
  data () {
    return {
      msg: 'Main Front First Business Card'
    }
  },
  components:{
    VueTyper, FontAwesomeIcon
  },
  methods: {
    wavy(){
          
    }
  }
}
</script>

<style >
.far{
  padding-left: 10px;
}
.resume{
    display: flex;
    align-items: flex-end;
    /* justify-content: center; */
    width: 100%;
    height: 20%;
    transition: 0.5s;
}

.resume:hover{
     opacity: 0.5;
    /* transform: scale(1.1); */
    transition: 0.5s;
}
#aikresume{
  color: white;
  /* text-decoration: none; */
  font-family: Product Sans, 'Space Mono', monospace;
  font-size: 1.5vw;
}

.vue-typer{
  font-family: Product Sans, 'Space Mono', monospace;
  color: #FFF !important;
}

.vue-typer .custom.char {
  color: #FFF;
  text-shadow: 2px 2px 30px white, 1px 1px 70px white; 
}

.vue-typer .custom.caret {
  width: 10px;
  background-color: white;
}

#mainComponent{
  /* padding: 5vmin; */
  /* padding-top: 8vmin; */
  /* overflow: hidden; */

}
.main{
    /* background-color: rgb(13, 87, 113); */
    height: 70vh;
    /* padding-left: 5vmin; */
    /* border-radius: 15px 15px 0 0 ; */
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 60% 40%;
}

.profile{
      display: flex;
      align-self: center;
      justify-self: center;
      border-radius: 50%;
      
      height: 33vmin;
      width: 33vmin;
      background-image: url("../assets/profile.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 10%;
      /* border: 10px white solid; */
      box-shadow: 0 19px 38px rgba(0,0,0,1), 0 15px 12px rgba(0,0,0,1);
}

.left-col{
  grid-column: 1/2;
  padding-left: 10vmin;
}

.right-col{
  grid-column: 2/3;
}

.name{
  position: relative;
  padding-top: 3vmin;
  /* left: -100px; */
  margin: 0;
  color: white;
  letter-spacing: 10px;
  font-family: Product Sans, 'Space Mono', monospace;
  /* word-spacing: 100vmin; */
  font-size: 10vmin;
  font-weight: bolder;
  /* border-bottom: 10px white solid; */
  /* width: fit-content; */
  /* animation: type 4s steps(60, end);  */
  text-shadow: 
    0 -1px 0 #fff, 
    0 1px 0 #2e2e2e, 
    0 2px 0 #2c2c2c, 
    0 3px 0 #2a2a2a, 
    0 4px 0 #282828, 
    0 5px 0 #262626, 
    0 6px 0 #242424, 
    0 7px 0 #222, 
    0 8px 0 #202020, 
    0 9px 0 #1e1e1e, 
    0 10px 0 #1c1c1c, 
    0 11px 0 #1a1a1a, 
    0 12px 0 #181818, 
    0 13px 0 #161616, 
    0 14px 0 #141414, 
    0 15px 0 #121212, 
    0 22px 30px rgba(0, 0, 0, 0.9);
}

.jobtitle{
    color: white;
    font-size: 7vmin;
    /* text-decoration: underline; */
        position: relative;
    top: 60px;
}

.tagline{
    color: white;
    font-size: 2vmin;
    font-weight: 300;
    font-family: 'Open Sans', sans-serif;

}

.location{
  color: white;
  font-size: 2vmin;
  font-weight: 300;
  font-family: 'Product Sans', sans-serif;
  padding-top: 60px;
}

.canvas-wrap {
  max-width: 100%;
  overflow: hidden;
  position: relative;
}

canvas {
  display: block;
}

@font-face {
  font-family: 'Product Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Product Sans'), local('ProductSans-Regular'), url(https://fonts.gstatic.com/s/productsans/v9/pxiDypQkot1TnFhsFMOfGShVE9eOcEg.woff2) format('woff2');
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}

.wave-bottom-container{
  z-index: 50;
  overflow: hidden;
  width: 100%;
  height: 30%;
  position: absolute;
  /* bottom: 0.7px; */
  bottom: 95px;

}
.wave {
  background: url("../assets/curve.svg") repeat-x; 
  width: 200vw; 
  height: 10vh; 
  bottom: 131.5px;
  position: absolute;
  /* right: 0px; */
  animation: wave 14s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite ;
  transform: translate3d(0, 0, 0) scaleY(-1);
}

.wave2 {
  /* // top: -175px; */
  animation: wave 14s cubic-bezier( 0.36, 0.45, 0.63, 0.53) -1s infinite ;
  animation-direction: reverse;
  left: 400px;
  opacity: 1;
}

@keyframes wave {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: -1600px;
  }

}
@keyframes wave2 {
  0% {
    margin-left: -1600px;
  }
  100% {
    margin-left: 0px;
  }
  
}

@keyframes swell {
  0%, 100% {
    transform: translate3d(0,-25px,0) scaleY(-1);
  }
  50% {
    transform: translate3d(0,5px,0) scaleY(-1);
  }
}

</style>



